    position: relative;
    flex-direction: row;
}

.white-key {
    width: 20px;
    height: 60px;
    border: 2px solid black;
    margin-left: -4px;
    outline: none;
}

.black-key {
    position: absolute;
    width: 12px;
    height: 40px;
    border: 1px solid black;
    /* background-image: linear-gradient(#000000, #303030); */
    outline: none;
}

